	import orderList from "../../components/order-list/order-list.vue"
    import cmdIcon from "@/components/cmd-icon/cmd-icon.vue"
	import uniPopup from '@/components/uni-popup/uni-popup.vue'
	import uniTag from "@/components/uni-tag/uni-tag.vue"
	export default {
		components:{
			orderList,
			cmdIcon,
			uniPopup,
			uniTag
		},
		data() {
			return {
				type:'',
				message:"支付成功后，商家确认发货前可退，已发货后不可退",
				address:"陕西省西安市雁塔区高新四路和科技路交叉口处 高新九号广场10号楼",
				deliveryType:"快递运输",
				deliveryDate:"工作日与周末都可收货",
				tagHome:true,
				tagSchool:false,
				tagCompany:false,
				tagDefault:false,
				tagAnother:false,
				tag:[
					{
						tagId:0,
						tagText:"快速配送",
						tagType:"success"
					},
					{
						tagId:1,
						tagText:"上门自提",
						tagType:"default"
					},
				],
				tagNext:[
					{
						tagId:0,
						tagText:"工作日与周末都可收货",
						tagType:"default"
					},
					{
						tagId:1,
						tagText:"只可工作日收货",
						tagType:"default"
					},
					{
						tagId:2,
						tagText:"只可周末收货",
						tagType:"success"
					},
				],
				
			}
		},
		
		onLoad() {

		},
		methods: {
			//弹框弹出和取消事件
			togglePopup(type, open) {
				this.type = type;
				this.$refs[open].open();
			},	
			cancel(type) {
				this.$refs[type].close()
			},
			//弹框状态
			change(e) {
				console.log(e.show)
			},
			//特产页面选择配送方式点击事件
			bindClick(text,type,tagId){
				console.log("点击成功"+text+type+tagId);
				this.deliveryType = text;
				let tagIdNext = 0;
				if(tagId == 0){
					tagIdNext = 1;
				}else{
					tagIdNext = 0;
				}
				if(type == "default"){
					this.tag[tagId].tagType = "success";
					this.tag[tagIdNext].tagType = "default"
				}
			},
			//特产页面选择送货时间点击事件
			bindClick1(text,type,tagId){
				console.log("点击成功"+text+type+tagId);
				this.deliveryDate = text;
				let tagId1 = 0;
				let tagId2 = 0;
				if(tagId == 0){
					tagId1 = 1;
					tagId2 = 2;
				}else if(tagId == 1){
					tagId1 = 0;
					tagId2 = 2;
				}else{
					tagId1 = 0;
					tagId2 = 1;
				}
				if(type == "default"){
					this.tagNext[tagId].tagType = "success";
					this.tagNext[tagId1].tagType = "default"
					this.tagNext[tagId2].tagType = "default"
				}
			},
			//点击提交订单跳转到支付订单页面
			goPayPage(){
				uni.navigateTo({
					url:"goodsPayPage"
				})
			},
			//点击地址部分跳转到地址页面
			goAddressPage(){
				console.log("跳转至地址页面")
				uni.navigateTo({
					url:"address"
				})
			}
				

		}
	}
